.content.about .container {
  height: 100%;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  min-height: 70rem;
}
.content.about .container .title {
  margin: 0 0 4rem;
  font-weight: 900;
  text-transform: uppercase;
  word-spacing: 0.3rem;
  line-height: 90%;
  transition: $transition;
}
.content.about .container .title span {
  display: block;
}
